import React, { Component } from 'react'
import {
    NavLink,
} from "react-router-dom";

export class Navbar extends Component {
    search = () =>{
        return "cricket"
    }
    render() {
        return (
            <div>
                <nav className="navbar navbar-expand-lg navbar-dark bg-dark" >
                    <div className="container-fluid">
                        <NavLink className="navbar-brand" style={{ fontSize: "24px", paddingTop: "0", paddingBottom: "0" }} exact to="/">Newsapp</NavLink>
                        <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                            <span className="navbar-toggler-icon"></span>
                        </button>
                        <div className="collapse navbar-collapse" id="navbarSupportedContent" style={{ marginLeft: "100px" }}>
                            <ul className="navbar-nav me-auto mb-2 mb-lg-0">
                                <li className="nav-item">
                                    <NavLink className="nav-link" style={{ paddingRight: "40px", fontSize: "18px" }} exact to="/" >Home</NavLink>
                                </li>
                                <li className="nav-item" >
                                    <NavLink className="nav-link" style={{ paddingRight: "40px", fontSize: '18px' }} exact to="/business">Business</NavLink>
                                </li>
                                <li className="nav-item">
                                    <NavLink className="nav-link" style={{ paddingRight: "40px", fontSize: '18px' }} exact to="/entertainment">Entertainment</NavLink>
                                </li>
                                <li className="nav-item">
                                    <NavLink className="nav-link" style={{ paddingRight: "40px", fontSize: '18px' }} exact to="/health">Health</NavLink>
                                </li>
                                <li className="nav-item">
                                    <NavLink className="nav-link" style={{ paddingRight: "40px", fontSize: '18px' }} exact to="/science">Science</NavLink>
                                </li>
                                <li className="nav-item">
                                    <NavLink className="nav-link" style={{ paddingRight: "40px", fontSize: '18px' }} exact to="/sports">Sports</NavLink>
                                </li>
                                <li className="nav-item">
                                    <NavLink className="nav-link" style={{ paddingRight: "40px", fontSize: '18px' }} exact to="/technology">Technology</NavLink>
                                </li>
                            </ul>
                        </div>
                    </div>
                </nav>
            </div>
        )
    }
}
export default Navbar
